<template>
  <div id="login">
    <b-container>
      <b-card no-body class="overflow-hidden">
        <b-row no-gutters>
          <b-col md="6">
            <img src="../assets/login.gif" style="width:100%">
          </b-col>
          <b-col md="6">
            <b-card-body>
              <h1 class="text-center">登入</h1>
              <b-form @submit="submit">
                <b-form-group
                  label="帳號"
                  label-for="input-account"
                  description="帳號長度為 4 - 20 個字"
                  invalid-feedback="帳號格式不符"
                  :state="state('account')">
                  <b-form-input id="input-account" type="text" v-model="account" :state="state('account')">
                  </b-form-input>
                </b-form-group>
                <b-form-group
                  label="密碼"
                  label-for="input-password"
                  description="密碼長度為 4 - 20 個字"
                  invalid-feedback="密碼格式不符"
                  :state="state('password')">
                  <b-form-input id="input-password" type="password" v-model="password" :state="state('password')">
                  </b-form-input>
                </b-form-group>
                <b-button type="submit" variant="primary">
                  登入
                </b-button>
              </b-form>
            </b-card-body>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      account: '',
      password: ''
    }
  },
  methods: {
    state (type) {
      if (type === 'account') {
        if (this.account.length < 4 || this.account.length > 20) {
          return false
        } else {
          return true
        }
      } else if (type === 'password') {
        if (this.password.length < 4 || this.password.length > 20) {
          return false
        } else {
          return true
        }
      }
    },
    submit (event) {
      event.preventDefault()
      if (this.account.length < 4 || this.account.length > 20) {
        alert('帳號格式不符')
      } else if (this.password.length < 4 || this.password.length > 20) {
        alert('密碼格式不符')
      }
      this.axios.post(
        process.env.VUE_APP_APIURL + '/login',
        { account: this.account, password: this.password }
      )
        .then(response => {
          const data = response.data
          if (data.success) {
            // 如果回來的資料 success 是 true
            alert('登入成功')
            // 呼叫 vuex 的登入
            this.$store.commit('login', data.result)
            if (this.account === '1234') {
              this.$router.push('/administrator')
            } else {
              // 跳到首頁
              this.$router.push('/')
            }
          } else {
            // 不是就顯示回來的 message
            alert(data.message)
          }
        })
        .catch(error => {
          // 如果回來的狀態不是 200，顯示回來的 message
          alert(error.response.data.message)
        })
    }
  }
}
</script>
